
































import Vue from 'vue'
import {Component, Prop, Ref, Provide, Watch} from 'vue-property-decorator'


@Component({name:'Sponsor',components:{}})
export default class Sponsor extends Vue{
    constructor(){
    super()
    }


    @Prop()
    model: number

    modelindex: number = 0

    colors: string[] = [
        'indigo',
        'warning',
        'pink darken-2',
        'red lighten-1',
        'deep-purple accent-4',
        ]
    slides: string[] =[
        'First',
        'Second',
        'Third',
        'Fourth',
        'Fifth',
    ]

    @Watch('model')
    onModelChange(){
        if(this.model==-1){
        }else{
            this.modelindex = this.model
        }
    }

}
