












































































































































































































































import Vue from 'vue'
import {Component, Prop, Ref, Provide, Watch, Emit} from 'vue-property-decorator'
import {COLORS,COLORS_R,COLORS_O,COLORS_G,COLORS_B,COLORS_Y,COLORS_W} from '../../plugins/v4/cuber/define'


@Component({name:'Account',components:{}})
export default class Account extends Vue{
    width: number = 0;
    height: number = 0;
    size: number = 0;


    tab:string = ''
    colord: boolean = false
    items =  [
                { tab: 'One', content: '' },
                { tab: 'Two', content: '' },
                { tab: 'Three', content: '' }
            ]
    // 
    clickcolorindex: number = null
    bottomlist = [
        {name:'',value:'blue',color:'indigo'},
        {name:'',value:'green',color:'green'},
        {name:'',value:'yellow',color:'yellow'},
        {name:'',value:'white',color:'grey lighten-1'},
        {name:'',value:'orange',color:'orange'},
        {name:'',value:'red',color:'red'}
        ]
    colorlist = [
        {name:'',value:'blue',color:'indigo'},
        {name:'',value:'green',color:'green'},
        {name:'',value:'yellow',color:'yellow'},
        {name:'',value:'white',color:'grey lighten-1'},
        {name:'',value:'orange',color:'orange'},
        {name:'',value:'red',color:'red'}
        ]
    bottom: string = ''

    facelist = [
        {name:'',value:'blue',color:'indigo'},
        {name:'',value:'green',color:'green'},
        {name:'',value:'yellow',color:'yellow'},
        {name:'',value:'white',color:'white'},
        {name:'',value:'orange',color:'orange'},
        {name:'',value:'red',color:'red'}
        ]
    facelist2 = [
        {name:'',value:'blue',color:'blue'},
        {name:'',value:'green',color:'light-green'},
        {name:'',value:'yellow',color:'amber'},
        {name:'',value:'white',color:'grey'},
        {name:'',value:'orange',color:'orange'},
        {name:'',value:'red',color:'pink'}
        ]

    stylelist = [
        ' lighten-4',
        ' lighten-3',
        ' lighten-2',
        ' lighten-1',
        ' ',
        ' darken-1',
        ' darken-2',
        ' darken-3',
        ' darken-4',
    ]

    palette: string[] = [
        // YELLOW
        "#FFD600",
        "#FFFF00",
        "#FFFF8D",
        "#FEFE00",
        "#FDD835",
        "#FFC107",
        // ORANGE
        "#FF6D00",
        "#FFA100",
        "#FB8C00",
        "#F57C00",
        "#EF6C00",
        "#E65100",
        // RED
        "#B71C1C",
        "#FF0000",
        "#EE0000",
        "#DD0000",
        "#CC0000",
        "#BF360C",
        // BLUE
        "#0D47A1",
        "#0000FF",
        "#88DDFF",
        "#03A9F4",
        "#1976D2",
        "#1A237E",
        // GREEN
        "#00A020",
        "#00FF00",
        "#76FF03",
        "#00D800",
        "#00A000",
        "#006600",
        // P
        "#FF4081",
        "#EEE8AA",
        "#FF99FF",
        "#A83DD9",
        "#607D8B",
        "#885500",
        // WB
        "#202020",
        "#000000",
        "#606060",
        "#A0A0A0",
        "#D0D0D0",
        "#FFFFFF",
  ];

    snackbar: boolean = false
    message: string = ''
    timeout = 2000

    @Emit('setsponsorindex')
    setSponsorIndex(value){
        return {
            data : value
        }
    }

    @Emit('switch2cubesample')
    Switch2CubeSample(){
        return true
    }

    @Emit('setthemecolor')
    setthemecolor(data){
        return data
    }

    @Emit('resetthemecolors')
    resetThemeColors(){
        this.getlocalstoragecolors()
        this.snackbar = true
        this.message = ``
        return true
    }

    @Emit('savethemecolors')
    saveThemeColors(){
        this.snackbar = true
        this.message = ``
        return true
    }


    @Watch('tab')
    onTabChange(){

        // tabsponsor
        // this.setSponsorIndex(0)

        // sponsor  
        this.Switch2CubeSample()
    }

    resize() {
        this.width = window.innerWidth;
        this.height = window.innerHeight;
        this.size = Math.ceil(Math.min(this.width / 6, this.height / 12));
        }

    mounted(){
        this.bottom = window.localStorage.getItem('bottomlayer')
        this.resize()
        this.getlocalstoragecolors()

    }
    setbottomlayer(layername){
        window.localStorage.setItem('bottomlayer', layername)
        this.bottom = layername
        switch(layername){
            case '':
                this.setthemecolor(COLORS_B)
                break
            case '':
                this.setthemecolor(COLORS_G)
                break
            case '':
                this.setthemecolor(COLORS_Y)
                break
            case '':
                this.setthemecolor(COLORS_W)
                break
            case '':
                this.setthemecolor(COLORS_O)
                break
            case '':
                this.setthemecolor(COLORS_R)
                break
            default:
                break
        }



        // this.snackbar = true
        // this.message = `${layername}`
    }

    opencolorselect(index){
        this.colord = true
        this.clickcolorindex = index
    }

    getlocalstoragecolors(){
        let themecolor = JSON.parse(window.localStorage.getItem('cubeconfig:casegroup')).themeconfig.colors        
        this.colorlist[0].color = themecolor.B
        this.colorlist[1].color = themecolor.F
        this.colorlist[2].color = themecolor.U
        this.colorlist[3].color = themecolor.D
        this.colorlist[4].color = themecolor.L
        this.colorlist[5].color = themecolor.R
    }

    setcolor(color){
        this.colorlist[this.clickcolorindex].color = color
        switch(this.clickcolorindex){
            case 0:
                this.setthemecolor({B:color})
                break
            case 1:
                this.setthemecolor({F:color})
                break
            case 2:
                this.setthemecolor({U:color})
                break
            case 3:
                this.setthemecolor({D:color})
                break
            case 4:
                this.setthemecolor({R:color})
                break
            case 5:
                this.setthemecolor({L:color})
                break
            default:
                break
        }
        this.colord = false
    }

    

    // 
    setcolorset(el,oneset){
        let eles = el.currentTarget.getElementsByClassName('facecolor')
        let colors = []
        for (let ele of eles){
            let color = window.getComputedStyle(ele).backgroundColor
            colors.push(color)
        }

        for(let index in colors){
            this.colorlist[index].color = colors[index]
        }

        let colorsobj = {
            B: colors[0],
            F: colors[1],
            U: colors[2],
            D: colors[3],
            L: colors[4],
            R: colors[5]
        }   
        this.setthemecolor(colorsobj) 

    }

}

