







































import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";
import { TwistAction, TwistNode } from "../../cuber/twister";
import {Cuber} from "../../cuber";

@Component({
  name: 'PLayer',
  components: {}
})

export default class Player extends Vue {
  @Prop({ required: false, default: false })
  disable: boolean;


  @Prop()
  autoplay: boolean

  @Prop()
  loop: boolean

  cuber: Cuber

  size: number = 0;
  constructor() {
    super();
  }

  mounted() {
    // console.log(this.cubename)
    // console.log(this.$cuberender)
    // this.cuber = this.$cuberender.pagecubes[this.cubename].cuber
    // setTimeout(() => {
    //   this.cuber.world.callbacks.push(() => {
    //   this.callback();
    //   });
    // }, 2000);

    // console.log(this.cuber)
    
  }

  resize(size: number) {
    this.size = size;
  }

  get style() {
    return {
      width: this.size + "px",
      height: this.size + "px",
      "min-width": "0%",
      "min-height": "0%",
      "text-transform": "none",
      flex: 1
    };
  }




  playing: boolean = false;
  progress: number = 0;
  @Watch("progress")
  onProgressChange() {
    this.cuber.controller.lock = this.progress > 0;
  }

  scene: string = "";
  @Watch("scene")
  onSceneChange() {
    this.init();
  }

  action: string = "";
  actions: TwistAction[] = [];
  @Watch("action")
  onActionChange() {
    this.actions = new TwistNode(this.action).parse();
    this.init();
  }

  init() {
    // this.cuber.controller.lock = false;
    this.playing = false;
    this.progress = 0;
    this.cuber.controller.disable = false;
    this.cuber.twister.finish();
    this.cuber.twister.twist("#");
    let scene = this.scene == "^" ? "(" + this.action + ")'" : this.scene;
    this.cuber.twister.twist(scene, false, 1, true);
    this.cuber.history.clear();
    if(this.autoplay){
      setTimeout(() => {
        this.playing = true
        this.callback();
      }, 1000);
    }
  }

  finish() {
    this.init();
    this.cuber.twister.twist(this.action, false, 1, true);
    this.progress = this.actions.length;
  }

  callback() {
    if (this.playing) {
      if (this.progress == this.actions.length) {
        if (this.playing) {
          this.playing = false;
          setTimeout(() => {
              if(this.loop){
              this.init()
              }
          }, 2000);
        }
        return;
      }
      let action = this.actions[this.progress];
      this.progress++;
      this.cuber.twister.twist(action.exp, action.reverse, action.times, false);
    }
  }

  toggle() {
    if (this.playing) {
      this.playing = false;
    } else {
      if (this.progress == 0) {
        this.init();
      }
      this.playing = true;
      this.callback();
    }
  }

  forward() {
    if (this.progress == this.actions.length) {
      return;
    }
    if (this.progress == 0) {
      this.init();
    }
    this.playing = false;
    let action = this.actions[this.progress];
    this.progress++;
    this.cuber.twister.twist(action.exp, action.reverse, action.times);
  }

  backward() {
    if (this.progress == 0) {
      return;
    }
    this.playing = false;
    this.progress--;
    let action = this.actions[this.progress];
    this.cuber.twister.twist(action.exp, !action.reverse, action.times);
  }
}

